@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	background-color: #ebebeb;
	font-family: 'Roboto', sans-serif;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.mainContainer{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;text-align:center;-webkit-justify-content:center;justify-content:center;width:100vw;max-width:600px;height:100vh;margin:0 auto}.mainContainer h1{margin:20px auto -100px auto}.buttonContaines{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-top:10px}.buttonContaines .all__buttons{margin:1.5%}.mainButton{padding:10px 15px;-webkit-align-items:center;align-items:center;font-size:x-large;border:none;color:#000;background-color:#fff;border-radius:5px}.mainButton:hover{background-color:#2c6fd3;color:#fff}.Joke__Box{height:auto;width:auto;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.Joke__Box .Joke__Title{font-weight:700;margin:30px;color:#313131}.Joke__Box .Joke__Title span{text-transform:capitalize;color:#ee3a34;font-size:xx-large}.Joke__Box .joke__Area{overflow:hidden;margin:10px;height:30vh;border-radius:10px;background-color:#2c6fd3;display:-webkit-flex;display:flex;padding:20px}.Joke__Box .joke__Area h3{font-weight:400;color:#ece8e8;background-color:#2c6fd3;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin:auto;text-align:justify;line-height:2rem}.Joke__Box .new__joke{outline:none;border:1px solid #dc5be7;margin:10px;color:#3c71e4;padding:20px;width:90%;border-radius:5px;outline:none;font-size:x-large}

